import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Header from '@/Header'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/',
      name: 'Header',
      component: Header
    }
  ]
})



