<template>
  <div class="wrapper">
    <section id="intro">
      <b-container class="bv-example-row">
        <b-row>
            <b-col>
              <transition name="slide-in">
                <img :src="IntroCenterImg">
              </transition>
            </b-col>
        </b-row>
      </b-container>
    </section>
     <section id="about">
      <b-container class="bv-example-row">
        <b-row>
            <b-col>
             
            </b-col>
        </b-row>
      </b-container>
    </section>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      msg: 'Welcome to KING Test Vue.js App',
      IntroCenterImg: '../../static/img/Elf.png',
    }
  },
  mounted(){
    let self = this;
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

#intro{
  overflow: hidden;
  position: relative;
  height: 100vh;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../../static/img/HomeBG.jpg);
  img{
    top: 20vh;
    left: 800px;
    position: absolute;
    animation-name: slide-in;
    animation-duration: 5s;
  }
}
#about{
  height: 800px;
}
@keyframes slide-in{
  from {left: -300px;top: 100vh}
  to {left:800px;top: 20vh}
}
</style>



